.btn-outline-light{
  color: unset;
  border-color: unset;
}

.btn-outline-light:hover{
  background: rgba(167, 69, 69, 0.5);
}


.weather-box {
  padding: 20px;
  border-radius: 6px;
  box-shadow: 0 0 12px 0 rgb(0 0 0 / 20%);
  background: white;
  margin-bottom: 30px;
}

.overview {
  /* background: rgba(255, 255, 255, 0.2) !important; */
  background: hsla(0,0%,100%,.1)!important;
}

.active {
  border-bottom: solid 1px;
}

.inactive {
  border: 0;
}

html {
  height: 100%;
}


body {
  font-family: Roboto !important;
  height: 100%;
}

#root{
  height: 100%;
}

.container{
  /* justify-content: center; */
  min-height: 100%;
  /* min-width: 400px!important; */
}
.search-input{
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.7);
  padding: 5px 38px 5px 6px;
}
.search-input::placeholder {
  color: black; 
}

.search-input:focus::placeholder{
  color: transparent;
}

.weather-items {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 49%;
  min-width: 300px;
  height: 40px;
  display: flex;
  align-items: center;
  border-top: solid 1px #dedede;
  
}


.weather-items:nth-child(odd){
  margin-right: 6px;
}
.weather-items:nth-child(even){
  margin-left: 6px;
}

.weather-icons {
  width: 24px;
  height: 24px;
}

.forecast-items:first-child{
  font-weight: bold;
}
.forecast-items:nth-child(2){
  border-left: 1px solid #dedede !important;
  border-right: 1px solid #dedede !important;
}

#search-box {
  position: relative;
  /* animation: searchbox-appear 1s 1 ease-in-out; */
  margin-left: auto;
  margin-right: auto;
  top: 0px

}

#main-container{
  opacity: 1;
  transition: opacity 0.16s linear;
}

#main-container.modal-backdrop{
  opacity: 0;
}
.modal-backdrop{
  background-color: unset;
  position: relative;
}

.fade-in:not(.show){
  left:100%;
  opacity: 0;
  /* animation: fade-in 0.5s linear; */
}

.fade-in{
  transition: left 0.5s linear,  opacity 0.5s linear;
}

.fade-out{
  animation: fade-out 0.5s linear;
}

@-webkit-keyframes fade-in {
  from{ 
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade-in {
  from{ 
    top:100%;
    opacity: 0;
    
  }
  to {
    top:0%;
    opacity: 1;
  }
}

@keyframes fade-out {
  from{ 
    top:0%;
    opacity: 1;
  }
  to {
    top:100%;
    opacity: 0;
  }
}

@media screen and (max-width: 768px){
  .weather-items{
    margin: 0px !important;
  }

  #hourly-forecast {
    padding-bottom: 5px;
  }

  #hourly-forecast::-webkit-scrollbar {
    height: 5px;
  }

  #hourly-forecast::-webkit-scrollbar-thumb{
    border-radius: 2.5px;
  }
  
  /* Track */
  #hourly-forecast::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0)
  }

  /* Handle */
  #hourly-forecast::-webkit-scrollbar-thumb {
    background: #888; 
  }
  
  /* Handle on hover */
  #hourly-forecast::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }
}


@media screen and (min-width: 768px) {
  #hourly-forecast {
    padding-bottom: 10px;
  }

  #hourly-forecast::-webkit-scrollbar {
    height: 10px;
  }

  #hourly-forecast::-webkit-scrollbar-thumb{
    border-radius: 5px;
  }
  
  /* Track */
  #hourly-forecast::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0)
  }
   
  /* Handle */
  #hourly-forecast::-webkit-scrollbar-thumb {
    background: #888; 
  }
  
  /* Handle on hover */
  #hourly-forecast::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }
}

